.search-form {
	max-width: 300px;
}

.search-form__search-bar {
	/* .search-form__search-bar */
}

.search-bar {
	position: relative;

}

.search-bar__label {
	/* .search-bar__label styles */
}

.search-bar__input {
	width: 100%;
	padding: 7px 35px;
	border: 2px solid transparent;
	font-size: 18px;
	color: var(--dark-color-100);
	border-radius: 4px;
	transition: border var(--transition-time) var(--transition-easing);
}

.search-bar__input:hover:not([disabled]) {
	border: 2px solid var(--brand-color-200);
	transition: border var(--transition-time) var(--transition-easing);
}

.search-bar__close {
	left: 0;
}

.search-bar__find {
	right: 0;
}

.search-bar__close,
.search-bar__find {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}

.search-bar__close>svg,
.search-bar__find>svg {
	width: 25px;
	height: 25px;
}